import moment from 'moment'

const columns = [
  {
    title: 'E-Posta',
    dataIndex: 'User',
    width: '40%',
  },
  {
    title: 'Referans Geliri',
    dataIndex: 'Value',
    width: '40%',
  },
  {
    title: 'Tarih',
    dataIndex: 'DateTime',
    width: '100%',
  },
]
const mobileColumns = [
  {
    title: 'E-Posta',
    dataIndex: 'User',
    width: '100%',
  },
  {
    title: 'Referans Geliri',
    dataIndex: 'Value',
    width: '100%',
  },
]
export default {
  name: 'ReferenceHistory',
  data() {
    return {
      columns,
      referenceDetail: [],
      isMobile: null,
    }
  },
  mounted() {
    this.getReferencePageDetail()

    this.isMobile = window.innerWidth < 1000

    window.addEventListener('resize', () => {
      this.isMobile = window.innerWidth < 1000
    })
  },
  filters: {
    dateFormat(val) {
      return moment(val).format('yyy-mm-DD hh:mm:ss')
    },
  },
  methods: {
    dateFormater(val) {
      return moment(val).format('yyy-mm-DD hh:mm:ss')
    },
    async getReferencePageDetail() {
      try {
        let authHeaders = {
          Authorization: 'Bearer ' + this.$cookies.get('Token'),
        }

        const response = await this.$axios.get(
          this.$config.FINANCE_API_URL +
            `/GetReferencePageDetail?Offset=0&RowSize=10`,
          {
            headers: authHeaders,
          }
        )

        if (response.data) {
          response.data.AwardList.map(res=>{
            res.DateTime = this.dateFormater(res.DateTime);
          })

          this.referenceDetail = response.data.AwardList;
          
        }
      } catch (e) {
        
      }
    },
    handleTableChange() {},
  },
}
